@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}
body{
  background-color: #B2C8DF;
}


.container{
    padding: 4% 2%;
}
@media only screen and (max-width: 600px) {
    .container{
      padding: 4% 10%;
    }
}
.term {
    text-align: center;
    max-width: 20rem;
    margin-bottom: 3rem;
    padding: 2rem;
    border-radius: 3px;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.05), 0 5px 20px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    transition: all 100ms ease-in-out;
}
.term > dt {
    margin-bottom: 1rem;
  }
  
  dt {
    color: #2ec2b0;
    line-height: 1;
  }
  
  dt span {
    font-size: 200%;
    font-weight: 900;
  }
  
  .emoji {
    margin: 0 auto 20px;
    display: block;
    font-size: 400%;
  }
  
  dd {
    font-size: 92.5%;
    margin-left: 0;
  }
  
  dd dt {
    margin-top: 1rem;
    margin-bottom: 0.25rem;
    font-weight: 600;
    font-size: inherit;
  }
  
  dd dl {
    font-size: 90%;
    margin: 1rem auto 0 auto;
    letter-spacing: 0.33px;
    max-width: 15rem;
  }
  
  dd dl .term {
    margin-bottom: 1rem;
  }
.new-emoji{
    background-color: rgba(255, 255, 255, 0.286);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    border-radius: 5px;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
    margin-bottom: 6%;
}
.new-emoji input{
    background-color: rgba(255, 255, 255, 0.067);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.new-emoji textarea{
    margin: 1.5% 0;
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0.067);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.new-emoji button{
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
    margin-left: 5px;
}


